import {Component} from '@angular/core';
import {HelpersService} from '../helpers.service';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {Storage} from "@ionic/storage";
import {Router} from "@angular/router";
import {ContentComponent} from "../content/content.component";
import {ModalController} from "@ionic/angular";

@Component({
    selector: 'app-tab3',
    templateUrl: 'tab3.page.html',
    styleUrls: ['tab3.page.scss']
})
export class Tab3Page {
    posts: any;
    path: string;
    private itemsCollection: AngularFirestoreCollection<any>;

    constructor(
        private helper: HelpersService,
        private db: AngularFirestore,
        private storage: Storage,
        private router: Router,
        private modalCtrl: ModalController,
    ) {
        this.ionViewWillEnter();
    }

    ionViewWillEnter() {
        this.storage.get('domainPath').then(path => {
            if (this.path !== path) {
                this.path = path;
                this.itemsCollection = this.db.collection(path + 'info', ref => ref.orderBy('importance', 'desc'));
                this.posts = this.itemsCollection.valueChanges();
            }
        });
    }

    showContentModal(data: object) {
        this.helper.showContentModal(data);
    }

    changeDomain() {
        this.router.navigate(['ChangeDomain'])
    }

    async createNewEvent() {
        const modal = await this.modalCtrl.create(
            {
                component: ContentComponent,
                componentProps: {
                    data: {newThing: true}
                }
            }
        );
        await modal.present();
        modal.onDidDismiss().then((e) => {
            if (e.data) {
                console.log("SEND THIS TO THE SERVER!", e.data);
                this.addItem(e.data);
            }
        });
    }

    private addItem(item: any) {
        this.itemsCollection.add(item);
    }

}
